import React from 'react';
import RoleAuthService from '../Service/RoleAuthService';
import AuthService from '../Service/AuthService';
import RoleService from '../Service/RoleService';
import {Multiselect} from  'multiselect-react-dropdown'

class RoleAuthComponentForUser extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            id:this.props.match.params.id,
            role:{},
            ownauths:[],
            auths:[],
            res:[]
     }  
     this.changeAuthHandler=this.changeAuthHandler.bind(this);
     this.multiselectRef=React.createRef();
   
    }
    
    componentDidMount(){
       RoleService.findById(this.state.id).then(res=>{
           this.setState({role:res.data});
       });
       RoleAuthService.findAllAuthByRoleid(this.state.id).then(res=>{
        this.setState({ownauths:res.data});
        });
        AuthService.findAllAuthOrderbyresid().then(res=>{
            this.setState({auths:res.data});
        });
     }

    changeAuthHandler=(a)=>{
        a.preventDefault();
        console.log("arrive here");
        this.setState({res:this.multiselectRef.current.getSelectedItems()});
        console.log(this.state.res);
        this.state.res.map(r=>{
            console.log(r);
            RoleAuthService.addRoleAuth({id:'',roleid:this.state.id,authid:r.id,isdeleted:0,createtime:'',updatetime:''}); 
        });
        console.log("arrive here 3");
    }

    render(){  
        this.state.auths.map(a=>{
            a.fullname=a.id+" "+a.authname;
            return a;
        } 
        )
       return(
        <div>
        <br></br>
        <h3 className="font-weight-bold text-secondary text-center">ID:{this.state.role.id}</h3>
        <h3 className="font-weight-bold text-secondary text-center">:{this.state.role.rolename}</h3>
        <div className="card bg-light mx-auto f-size" style={{width:"20rem"}}>
        <h5 className="card-header text-center"></h5>
        {
            this.state.ownauths.map(
                auth=>
                <div>:{auth.resname}, :{auth.authname}</div>
            )
        }
        </div>
        <div>
        <Multiselect options={this.state.auths} displayValue='fullname' value='id' groupBy='resname' placeholder='' emptyRecordMsg='' hidePlaceholder={true} showCheckbox={true} closeOnSelect={false} ref={this.multiselectRef}
        disablePreSelectedValues={true}/>
        </div>

        <div className="text-center">
       <button className="btn green-btn text-white font-weight-bold" onClick={this.changeAuthHandler}></button>
       </div>

        </div>
       )
    }
}
export default RoleAuthComponentForUser 